import {useState} from "react";
import axios from "axios";
import Swal from "sweetalert2";

function AddCategory() {
    const [form, setform] = useState({
        category: '',
        description: ''
    })

    const inputHandler = (e) => {
        setform({...form, [e.target.name]: e.target.value})
    }

    const resetButton = (e) => {
        setform({
            category: '',
            description: ''
        })
    }

    const addCategorySubmitButton = async (e) => {
        e.preventDefault()

        if (form.category === '' || form.description === '') {
            alert('Please fill all the fields')
        } else {
            const request = {
                ...form
            }
            const response = await axios.post('https://askthegeeks-nodeserver.onrender.com/add-category', request)
                .then(res =>{
                    if (res.data === "duplicate") {
                        Swal.fire(
                            'Category already exist!!',
                            "Add category that don't exist",
                            'error'
                        )
                    }
                    else {
                        resetButton()
                        Swal.fire(
                            'Added!',
                            'Category has been added.',
                            'success'
                        )
                    }
                })
        }
    }
    return(
        <>
            <div className="user-panel-main-bar">
                <div className="user-panel">
                    <div className="bg-gray p-3 rounded-rounded">
                        <h3 className="fs-17 text-center">Add Category</h3>
                    </div>
                    <form method="post" className="pt-20px">
                        <div className="settings-item">
                            <div className="divider"><span /></div>
                            <div className="row pt-4">
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Category Name</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" type="text" name="category" value={form.category}
                                                   onChange={inputHandler} placeholder="Enter Category Name" />
                                            <span className="la la-filter input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}

                                <div className="col-lg-6">
                                <div className="input-box">
                                    <label className="fs-13 text-black lh-20 fw-medium">Category Description</label>
                                    <div className="form-group">
                                        <input className="form-control form--control pl-40px" type="text" name="description" value={form.description}
                                               onChange={inputHandler} placeholder="Enter Category Description" />
                                        <span className="la la-pen input-icon" />
                                    </div>
                                </div>
                                </div>
                                <div className="col-lg-12">
                                    <div className="submit-btn-box pt-3">
                                        <button type={"submit"} className="btn theme-btn" onClick={addCategorySubmitButton}>Add Category</button>
                                    </div>
                                </div>{/* end col-lg-12 */}
                            </div>{/* end row */}
                        </div>
                    </form>
                </div>{/* end user-panel */}
            </div>
        </>
    );
}

export default AddCategory;