import {useState, useEffect} from "react";
import axios from "axios";
import AdminNav from "./AdminNav";
import { useNavigate } from "react-router-dom";

function EditAdmin(){
    let navigate = useNavigate();
    let user = localStorage.getItem('admin_edit');
    let [email, setEmail] = useState("");
    let [fullname, setFullname] = useState("");
    let [mobile, setMobile] = useState("");
    let [admintype, setAdmintype] = useState("");

    function GetDetails() {
        axios.get(`https://askthegeeks-nodeserver.onrender.com/get-user-details?email=${user}`)
            .then(res => {

                let User = res.data;
                setEmail(User[0].email);
                setFullname(User[0].fullname);
                setMobile(User[0].mobile);
                setAdmintype(User[0].admintype);
            })
    }


    useEffect(() => {
        GetDetails();
      }, []);

    function handleForm(e) {
        e.preventDefault();

        console.log(`${email} ${fullname} ${mobile} ${admintype}`);

        axios.post("https://askthegeeks-nodeserver.onrender.com/update-action", {
            email,
            mobile,
            fullname,
            admintype
        })
            .then(res => {

                if (res.data === "required") {
                    alert("All fields are required!!");
                } else {
                    setEmail("");
                    setFullname("");
                    setMobile("");
                    setAdmintype("");

                    alert(res.data);
                    navigate('/mainadmin');
                    GetDetails();

                }
            })
    }

    return(
        <>
        <AdminNav />
            <div className="modal-body">
            <form method="post" className="pt-20px">
                        <div className="">
                            {/* <div className="divider"><span /></div> */}
                            <div className="row pt-4">
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Email</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" value={email} placeholder="Enter your email address" onChange={(e) => setEmail(e.target.value)}
                                                   type="email" id="email" name={"email"} />
                                            <span className="la la-envelope input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Full Name</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" value={fullname} placeholder="Enter your full name" onChange={(e) => setFullname(e.target.value)}
                                                   type="text" id="fullname" name={"fullname"} />
                                            <span className="la la-user input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Mobile</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" value={mobile} placeholder="Enter your mobile number" onChange={(e) => setMobile(e.target.value)}
                                                   type="mobile" id="mobile" name={"mobile"} />
                                            <span className="la la-mobile input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}
                                <div className="col-lg-12">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Admin Type</label>
                                        <div className="form-group">
                                            <select value={admintype} onChange={(event) => setAdmintype(event.target.value)} name="admintype" id="admintype" className="custom-select custom--select">
                                                <option value="" selected>Select Admin type</option>
                                                <option value="admin">Admin</option>
                                                <option value="co-admin">Co-Admin</option>
                                            </select>

                                        </div>
                                    </div>
                                </div>{/* end col-lg-12 */}
                                <div className="col-lg-12">
                                    <div className="submit-btn-box pt-3">
                                        <button type={"button"} className="btn theme-btn" onClick={handleForm}>Update Admin</button>
                                    </div>
                                </div>{/* end col-lg-12 */}
                            </div>{/* end row */}
                        </div>
                    </form>
            </div>
        </>
    );
}

export default EditAdmin;