import {useState} from "react";
import axios from "axios";
import Swal from "sweetalert2";

function AddAdmin() {
    const [form, setform] = useState({
        email: '',
        password: '',
        fullname: '',
        mobile: '',
        admintype: ''
    })

    const inputHandler = (e) => {
        setform({...form, [e.target.name]: e.target.value})
    }

    const resetButton = (e) => {
        setform({
            email: '',
            password: '',
            fullname: '',
            mobile: '',
            admintype: ''
        })
    }

    const addAdminSubmitButton = async (e) => {
        e.preventDefault()

        if (form.email === '' || form.password === '' || form.fullname === '' || form.mobile === '' || form.admintype === '') {
            // alert('Please fill all the fields')
            Swal.fire(
                'Empty!',
                'Please fill all the fields.',
                'error'
            )
        } else {
            const request = {
                ...form
            }
            const response = await axios.post('https://askthegeeks-nodeserver.onrender.com/register-action', request)
                .then(res =>{
                    if (res.data === "duplicate") {
                        Swal.fire(
                            'Error!',
                            'Email already exist.',
                            'error'
                        )
                    }
                    else {
                        resetButton()
                    }
                })
        }
    }
    return(
        <>
            <div className="user-panel-main-bar">
                <div className="user-panel">
                    <div className="bg-gray p-3 rounded-rounded">
                        <h3 className="fs-17 text-center">Add Admin</h3>
                    </div>
                    <form method="post" className="pt-20px">
                        <div className="settings-item">
                            <div className="divider"><span /></div>
                            <div className="row pt-4">
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Email</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" type="email" name="email" value={form.email}
                                                   onChange={inputHandler} placeholder="Enter your email address" />
                                            <span className="la la-envelope input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Password</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" type="password" name="password" value={form.password}
                                                   onChange={inputHandler} placeholder="Enter your Password" />
                                            <span className="la la-key input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Full Name</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" type="text" name="fullname" value={form.fullname}
                                                   onChange={inputHandler} placeholder="Enter your Full Name" />
                                            <span className="la la-user input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Mobile</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" type="text" name="mobile" value={form.mobile}
                                                   onChange={inputHandler} placeholder="Enter your Mobile" />
                                            <span className="la la-mobile input-icon" />
                                        </div>
                                    </div>
                                </div>{/* end col-lg-6 */}
                                <div className="col-lg-12">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Admin Type</label>
                                        <div className="form-group">
                                            <select onChange={inputHandler} className="custom-select custom--select" name="admintype" id="admintype">
                                                <option value="" selected>Select Admin type</option>
                                                <option value="admin">Admin</option>
                                                <option value="co-admin">Co-Admin</option>
                                            </select>

                                        </div>
                                    </div>
                                </div>{/* end col-lg-12 */}
                                <div className="col-lg-12">
                                    <div className="submit-btn-box pt-3">
                                        <button type={"submit"} className="btn theme-btn" onClick={addAdminSubmitButton}>Add Admin</button>
                                    </div>
                                </div>{/* end col-lg-12 */}
                            </div>{/* end row */}
                        </div>
                    </form>
                </div>{/* end user-panel */}
            </div>
        </>
    );
}

export default AddAdmin;