import {useEffect, useState} from "react";
import axios from "axios";
import Swal from "sweetalert2";

function Members() {

    let [users, setUsers] = useState([]);

    function FetchUsers() {
        axios.get("https://askthegeeks-nodeserver.onrender.com/get-all-members")
            .then(res => {
                setUsers(res.data);
            })
    }

    useEffect(() => {
        FetchUsers();
    }, []);

    function manageUser(member_username) {
        Swal.fire({
            title: 'Are you sure to update?',
            text: "This will change users current status",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, Do it!'
        }).then((result) => {
            if (result.isConfirmed) {
                axios.get(`https://askthegeeks-nodeserver.onrender.com/ban-user?username=${member_username}`)
                    .then(res => {
                        console.log(res.data);

                        if (res.data === "banned") {
                            FetchUsers();
                            Swal.fire(
                                'Banned!',
                                'User has been banned.',
                                'success'
                            )
                        }
                        else if(res.data === "active") {
                            FetchUsers();
                            Swal.fire(
                                'Un-Banned!',
                                'User has been Un-Banned.',
                                'success'
                            )
                        }
                    });
            }
        })


    }

  return (
    <>
      <section>
        <div className="container">
          
            {/* <li className="nav-item"> */}
              <div
                className="tab-pane fade show active"
                id="manage-members-tab"
                role="tabpanel"
                aria-labelledby="edit-profile-tab"
              >

                  {/* details start */}
                  
                  <div className="user-panel-main-bar">
                  <div className="user-panel">
                    <div className="bg-gray p-3 rounded-rounded">
                        <h3 className="fs-17 text-center">All Members</h3>
                    </div>
                    <div className="row pt-4"/>
                    <div className="col-lg-6"/>
                    <form action="#" className="cart-form table-responsive px-2">

                        <table className="table generic-table">
                            <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Member Username</th>
                                <th scope="col">Member Email</th>
                                <th scope="col" className="text-center">Manage</th>
                            </tr>
                            </thead>
                            <tbody>
                            {
                                users.map((value, index) => {
                                    let buttonText;
                                    let buttonStyle;
                                    let iconStyle;
                                    if (value.status==='active') {
                                        buttonText = "Ban";
                                        buttonStyle = "btn btn-danger";
                                        iconStyle = "la la-ban"
                                    } else if(value.status==='banned'){
                                        buttonText = "Un-Ban";
                                        buttonStyle = "btn btn-success";
                                        iconStyle = "la la-check-double"
                                    }
                                    
                                    return (
                                        <tr key={index}>
                                            <td>{index + 1})</td>
                                            <td>{value.username}</td>
                                            <td>{value.email}</td>
                                            <td className="text-center">
                                                <button type={"button"} onClick={() => manageUser(value.username)} className={buttonStyle}><i className={iconStyle} /> {buttonText}</button>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                            </tbody>
                        </table>
                    </form>
                </div>
                </div>
                  {/* details end */}
                
              </div>
            {/* </li> */}
        
        </div>
      </section>
    </>
  );
}

export default Members;
