import AddCategory from "./AddCategory";
import ViewCategory from "./ViewCategory";

function Category() {


    return(
        <>
                <section>
                    <div className="container">
                        <ul className="nav nav-tabs generic-tabs generic--tabs generic--tabs-2 mt-4" id="admin" role="tablist">
                            <li className="nav-item">
                                <a className="nav-link active" id="edit-profile-tab" data-toggle="tab" href="#add-category" role="tab" aria-controls="edit-profile" aria-selected="true">Add Category</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="change-password-tab" data-toggle="tab" href="#view-category" role="tab" aria-controls="change-password" aria-selected="false">View Categories</a>
                            </li>
                        </ul>
                    </div>


                    <section className="user-details-area pt-40px pb-40px">
                        <div className="container">
                            <div className="">
                                <div className="">
                                    <div className="tab-content mb-50px" id="myTabContent">
                                        <div className="tab-pane fade show active" id="add-category" role="tabpanel" aria-labelledby="edit-profile-tab">
                                            <AddCategory/>
                                        </div>{/* end tab-pane */}
                                        <div className="tab-pane fade" id="view-category" role="tabpanel" aria-labelledby="change-password-tab">
                                            <ViewCategory/>
                                        </div>{/* end tab-pane */}
                                    </div>
                                </div>{/* end col-lg-9 */}

                            </div>{/* end row */}
                        </div>{/* end container */}
                    </section>
                </section>
        </>
    );
}

export default Category;