import { useMemo, useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import Pagination from "../pagination/Pagination";
import Sidebar from "../users/Sidebar";
import Swal from "sweetalert2";
import { useMemoOne, useCallbackOne } from 'use-memo-one';

function Questions(props) {
  const currentUser = localStorage.getItem("username");
  let PageSize = 5;
  let qidValue;
  let today = new Date();
  let [questions, setQuestions] = useState([]);

  const [currentPage, setCurrentPage] = useState(500);


  let currentTableData;

    currentTableData = useMemo(() => {
      const firstPageIndex = (currentPage - 1) * PageSize;
      const lastPageIndex = firstPageIndex + PageSize;
      return questions.slice(firstPageIndex, lastPageIndex);
    }, [currentPage]);


  function FetchQuestions() {
    axios.get("https://askthegeeks-nodeserver.onrender.com/fetch-questions").then(async (res) => {
      await setQuestions(res.data);
    });

  }

  function Cpage() {
    if (currentPage===500) {
      setCurrentPage(1);
    }
  }
  useEffect(() => {
    FetchQuestions();
  }, []);

  const navigate = useNavigate();
  
  function addquestion(currentUser) {
    axios.get(`https://askthegeeks-nodeserver.onrender.com/get-member-status?username=${currentUser}`)
              .then(res => {
                  console.log(res.data);
                  if (res.data==='active') {
                      navigate('/add-question');
                  } else if(res.data==='banned'){
                    Swal.fire(
                      'Banned!',
                      'You are banned to post Questions and Answers.',
                      'error'
                  )
                  }
              })
  }

  function questionDetails(qid) {
    console.log(qid);
    navigate("/question-details", { state: { id: qid } });
  }

function memberProfile(member_username) {
  let key = 'member_username';
                localStorage.setItem(key, member_username);
                navigate('/user');
}

  return (
    <>
      <section onLoad={Cpage} className="question-area pt-40px">
        <div className="container">
          <div className="row">
            <div className="col-lg-9">
              <div className="question-main-bar pb-45px">
                <div className="filters pb-4">
                  <div className="d-flex flex-wrap align-items-center justify-content-between pb-3">
                    <h3 className="fs-22 fw-medium">All Questions</h3>
                    <button
                      onClick={() => addquestion(currentUser)}
                      className="btn theme-btn theme-btn-sm"
                    >
                      Ask Question
                    </button>
                  </div>
                  
                </div>
                <div className="questions-snippet border-top border-top-gray">
                  {currentTableData.map((value, index) => {
                    const profilePic = value.photo;
                    qidValue = value.qid;
                    console.log(value.photo);
                    return (
                      <div
                        key={index}
                        className="media media-card rounded-0 shadow-none mb-0 bg-transparent py-3 px-0 border-bottom border-bottom-gray"
                      >
                        <div className="votes text-center votes-2">
                          <div className="vote-block">
                            <span className="vote-counts d-block text-center pr-0 lh-20 fw-medium">
                              3
                            </span>
                            <span className="vote-text d-block fs-13 lh-18">
                              votes
                            </span>
                          </div>
                          <div className="answer-block answered my-2">
                            <span className="answer-counts d-block lh-20 fw-medium">
                              {qidValue}
                            </span>
                            <span className="answer-text d-block fs-13 lh-18">
                              answers
                            </span>
                          </div>
                          <div className="view-block">
                            <span className="view-counts d-block lh-20 fw-medium">
                              12
                            </span>
                            <span className="view-text d-block fs-13 lh-18">
                              views
                            </span>
                          </div>
                        </div>
                        <div className="media-body">
                          <h5
                            onClick={() => questionDetails(value.qid)}
                            className="mb-2 fw-medium"
                          >
                            <a>{value.title}</a>
                          </h5>
                          <p className="mb-2 truncate lh-20 fs-15">
                            {value.description}
                          </p>
                          <div className="media media-card user-media align-items-center px-0 border-bottom-0 pb-0">
                            <a
                              href="user-profile.html"
                              className="media-img d-block"
                            >
                              <img
                                src={value.photo}
                                alt="avatar"
                              />
                            </a>
                            <div className="media-body d-flex flex-wrap align-items-center justify-content-between">
                              <div>
                                <h5 className="pb-1">
                                  <a onClick={()=>memberProfile(value.username)}>
                                    {value.username}
                                  </a>
                                </h5>
                                <div className="stats fs-12 d-flex align-items-center lh-18">
                                  <span
                                    className="text-black pr-2"
                                    title="Reputation score"
                                  >
                                    224
                                  </span>
                                </div>
                              </div>
                              <small className="meta d-block text-right">
                                <span className="text-black d-block lh-18">
                                  asked on
                                </span>
                                <span className="d-block lh-18 fs-12">
                                  {
                                    today.toISOString(value.date_of_posting).split("T")[0]
                                  }
                                </span>
                              </small>
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
                <Pagination
                  className="pagination-bar"
                  currentPage={currentPage}
                  totalCount={questions.length}
                  pageSize={PageSize}
                  onPageChange={(page) => setCurrentPage(page)}
                />
                
              </div>
              {/* end question-main-bar */}
            </div>
            {/* end col-lg-9 */}
            <Sidebar />
          </div>
          {/* end row */}
        </div>
        {/* end container */}
      </section>
    </>
  );
}
export default Questions;
