import { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
function Sidebar(props) {
  const navigate = useNavigate();
    let today = new Date();
    let [trendquestions, setTrendQuestions] = useState([]);
    let [ad, setAd] = useState([]);

    function FetchTrendQuestions() {
        axios.get("https://askthegeeks-nodeserver.onrender.com/fetch-trend-questions").then(async (res) => {
            setTrendQuestions(res.data);
        });
      }
    
    function FetchAd() {
      axios.get("https://askthegeeks-nodeserver.onrender.com/fetch-advertisement").then(async (res) => {
            setAd(res.data);
        });
    }
    
      useEffect(() => {
        FetchTrendQuestions();
        FetchAd();
      }, []);

      function questionDetails(qid) {
        if (window.location.pathname==="/question-details") {
          navigate("/question-details", { state: { id: qid } });
          window.location.reload();
        }
        else {
          navigate("/question-details", { state: { id: qid } });
        }
      }

    return(
        <>
            <div className="col-lg-3">
                <div className="sidebar">
                  <div className="card card-item">
                    <div className="card-body">
                      <h3 className="fs-17 pb-3">Number of Achievements</h3>
                      <div className="divider"><span /></div>
                      <div className="row no-gutters text-center">
                        <div className="col-lg-6 responsive-column-half">
                          <div className="icon-box pt-3">
                            <span className="fs-20 fw-bold text-color">980k</span>
                            <p className="fs-14">Questions</p>
                          </div>{/* end icon-box */}
                        </div>{/* end col-lg-6 */}
                        <div className="col-lg-6 responsive-column-half">
                          <div className="icon-box pt-3">
                            <span className="fs-20 fw-bold text-color-2">610k</span>
                            <p className="fs-14">Answers</p>
                          </div>{/* end icon-box */}
                        </div>{/* end col-lg-6 */}
                        <div className="col-lg-6 responsive-column-half">
                          <div className="icon-box pt-3">
                            <span className="fs-20 fw-bold text-color-3">650k</span>
                            <p className="fs-14">Answer accepted</p>
                          </div>{/* end icon-box */}
                        </div>{/* end col-lg-6 */}
                        <div className="col-lg-6 responsive-column-half">
                          <div className="icon-box pt-3">
                            <span className="fs-20 fw-bold text-color-4">320k</span>
                            <p className="fs-14">Users</p>
                          </div>{/* end icon-box */}
                        </div>{/* end col-lg-6 */}
                      </div>{/* end row */}
                    </div>
                  </div>{/* end card */}
                  <div className="card card-item">
                  
                    <div className="card-body">
                      <h3 className="fs-17 pb-3">Latest Questions</h3>
                      <div className="divider"><span /></div>
                      <div className="sidebar-questions pt-3">
                      {
                  trendquestions.map((value, index) => {
                    return (
                        <>
                        <div className="media media-card media--card media--card-2">
                          <div className="media-body">
                            <h5><a onClick={() => questionDetails(value.qid)}>{value.title}</a></h5>
                            <small className="meta">
                              <span className="pr-1">
                              {
                                    today
                                      .toISOString(value.date_of_posting)
                                      .split("T")[0]
                                  }
                              </span>
                            </small>
                          </div>
                        </div>{/* end media */}
                        </>
                        );
                    })}
                      </div>{/* end sidebar-questions */}
                    </div>
                    
                  </div>{/* end card */}
                  <div className="ad-card">
                    <h4 className="text-gray text-uppercase fs-13 pb-3 text-center">Advertisements</h4>
                    <div className="ad-banner mb-4 mx-auto">
                    {
                  ad.map((value) => {
                    return (
                        <>
                      <a target="_blank" href={value.ad_url}> <img src={value.ad_image} alt="" width={290} /> </a>
                      </>
                        );
                    })}
                    </div>
                  </div>{/* end ad-card */}
                </div>{/* end sidebar */}
                
              </div>
        </>
    );
}

export default Sidebar;