import UserNav from "../users/userNav";
import {useState , useEffect} from "react";
import axios from "axios";
import Swal from "sweetalert2";
import bubble from '../../assets/images/bubble.png';
import check from '../../assets/images/check.png';
import vote from '../../assets/images/vote.png';
function AddQuestion() {

  const currentUser = localStorage.getItem('username');

  
  let [category, setcategory] = useState([]);

    function FetchCategory() {
        axios.get("https://askthegeeks-nodeserver.onrender.com/fetch-category")
            .then(res => {
              setcategory(res.data);
              console.log(category);
            })
    }

    useEffect(() => {
        FetchCategory();
    }, []);


  const [question, setquestion] = useState({
    title: '',
    description: '',
    member_username: currentUser,
    category: ''
})

const inputHandler = (e) => {
    setquestion({...question, [e.target.name]: e.target.value})
}

const testFun = (e) => {
  console.log(question);
  console.log(currentUser);
  // console.log(question.member_username);
}

const resetButton = (e) => {
    setquestion({
        title: '',
        description: '',
        category: ''
    })
}

const addQuestionButton = async (e) => {
    e.preventDefault()

    if (question.title === '' || question.description === '' || question.category === '') {
        // alert('Please fill all the fields')
        Swal.fire(
          'Category not selected!',
          'Question category is not selected.',
          'error'
      )
    } else {
        const request = {
            ...question
        }
        const response = await axios.post('https://askthegeeks-nodeserver.onrender.com/add-question', request)
            .then(res =>{
                if (res.data === "Question added...") {
                  resetButton()
                  Swal.fire(
                      'Added!',
                      'Question has been added.',
                      'success'
                  )
                }
                else {
                    alert("Problem in adding question")
                }
            })
    }
}


    return(
        <>
        <UserNav />
            <div>
        {/*======================================
        START HERO AREA
======================================*/}
        <section className="hero-area bg-white shadow-sm overflow-hidden">
          <span className="stroke-shape stroke-shape-1" />
          <span className="stroke-shape stroke-shape-2" />
          <span className="stroke-shape stroke-shape-3" />
          <span className="stroke-shape stroke-shape-4" />
          <span className="stroke-shape stroke-shape-5" />
          <span className="stroke-shape stroke-shape-6" />
          <div className="container">
            <div className="hero-content pt-80px pb-80px">
              <h2 className="section-title">Ask a public question</h2>
              <svg className="svg-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 550 125"><defs><style dangerouslySetInnerHTML={{__html: ".cls-3,.cls-6,.cls-7{fill:none;stroke-miterlimit:10}.cls-3{stroke:#3cb1c6}.cls-4{fill:#fff}.cls-5{fill:#f9b851}.cls-6{stroke:#f48024}.cls-7{stroke:#bbc0c4;stroke-dasharray:5}" }} /></defs><g opacity=".5"><path fill="#cceaff" d="M232.73 73L217.5 92.89V73h-16V26h64v47H232.73z" /><path className="cls-3" d="M205 47V20.5h14.5M255.5 66.5h-18.51L221 86.06V66.5h-16V58M231.5 20.5H270v46h-6.5" /><path className="cls-4" d="M222.5 40.79v7.3l8.16 7.29 15.84-14.21V33.3l-15.84 14.31-8.16-6.82z" /><path className="cls-5" d="M374.36 76l-9.29 11.42-.26-11.42h-5.31V49h45v27h-30.14z" /><path className="cls-6" d="M379.5 46.5H402V55M402 62v11.5H372.02L363 84.92V73.5h-6v-27h11.5" /><path className="cls-4" d="M377.5 59h5v5h-5zM367.5 59h5v5h-5zM387.5 59h5v5h-5z" /><path className="cls-5" d="M180.25 67l5.16 6.77.14-6.77h2.95V51h-25v16h16.75z" /><path className="cls-6" d="M170.5 65.5h10.73l5.77 6.72V65.5h4V59M170.5 49.5H165V65M191 53v-3.5h-15.5" /><path className="cls-4" transform="rotate(-180 177 58.5)" d="M175.5 57h3v3h-3z" /><path className="cls-4" transform="rotate(-180 183 58.5)" d="M181.5 57h3v3h-3z" /><path className="cls-4" transform="rotate(-180 171 58.5)" d="M169.5 57h3v3h-3z" /><path className="cls-5" d="M486.95 77l15.55 19.89V77h16V30h-65v47H486.95z" /><path className="cls-6" d="M515 51V24.5h-14.5M464.5 70.5h18.51L499 90.06V70.5h16V62M488.5 24.5H450v46h6.5" /><path className="cls-4" d="M470.5 44.71V52l8.16 7.3 15.84-14.21v-7.87l-15.84 14.3-8.16-6.81z" /><path className="cls-3" d="M533.5 34.5h-2.14L527 40.34V34.5h-3v-12h4.5M545 30v5.5h-7.5M533.5 22.5H545V26M534 28.5h2v2h-2z" /><path className="cls-3" d="M529 28.5h2v2h-2zM539 28.5h2v2h-2zM280 30V16.5h7.5M305.5 39.5H296l-8 10v-10h-8V35M293.5 15.5H313v24h-3.5" /><path className="cls-3" d="M289 26.52v3.65l4.08 3.65 7.92-7.11v-3.93l-7.92 7.15-4.08-3.41z" /><path className="cls-6" d="M124.5 27.5h1.41l3.09 4.23V27.5h2v-10h-2.5M115 23v4.5h6.5M124.5 17.5H115V21" /><path className="cls-6" transform="rotate(-180 123 22.5)" d="M122 21.5h2v2h-2z" /><path className="cls-6" transform="rotate(-180 127 22.5)" d="M126 21.5h2v2h-2z" /><path className="cls-6" transform="rotate(-180 119 22.5)" d="M118 21.5h2v2h-2z" /><path className="cls-7" d="M188.5 78.19s4.69 18.91 27.5 16.28M131 32.67s21.64-2 33 15M271.12 69.19c23.92 31.24 55.21 35.18 90.64 19.3M402 43.5c.68-6.28 19.53-30.13 45.26-21M503.45 98.26c5.25.1 37.8-10.84 23.09-54.76M272.34 66.5s10.51 0 15.16-13.88" /><path className="cls-7" d="M97.22 79.67c14.33-2.39 42.51-3.18 55.87 12.65s58.91 20.93 64.49 3" /></g><path className="cls-3" d="M59.15 84.6v-1.04M71.6 84.81v3.73M53.39 82.51l-3.96-.54 2.96-21.6 27.37 3.75-2.96 21.6-20.05-2.75" /><path className="cls-3" d="M50.22 76.21l-2.31-.32a2 2 0 01-1.73-2.28l.63-4.61a2 2 0 012.3-1.74l2.3.31zM79.88 80.27L77.59 80l1.18-8.64 2.32.32a2 2 0 011.73 2.27l-.64 4.62a2 2 0 01-2.3 1.7zM66.6 79.11l-.2 1.44M69.48 79.5l-.2 1.44M63.72 78.71l-.2 1.44M60.84 78.32l-.2 1.44M57.96 77.93l-.2 1.44" /><ellipse className="cls-3" cx="66.45" cy="70.5" rx="2.87" ry="2.94" transform="rotate(-82.2 66.45 70.504)" /><ellipse className="cls-3" cx="56.76" cy="69.18" rx="2.87" ry="2.94" transform="rotate(-82.2 56.758 69.175)" /><path className="cls-3" d="M55.42 105.96v7.46M72.84 105.96v7.46M76.57 96.01v1.24M66.62 96.01v1.24M54.18 95.38h7.47v4.98h-7.47z" /><path className="cls-3" d="M81.55 100.98v4.36H46.71V87.92h5.28M57.43 79.39l-1.98 3" /><path className="cls-3" d="M54.31 90.61c-2.34-1.55-3.17-4.43-1.85-6.43l1.8-2.73 8.49 5.61L61 89.78c-1.37 2.01-4.34 2.38-6.69.83zM62.27 87.92h19.28v9.33" /><path className="cls-3" d="M88.32 96.44l1.28-4.17a4.4 4.4 0 00-4.45-4.35h-5.47M79.29 104.84l2.68-3.44M82.72 107.53l2.69-3.44M88.32 96.83c2.22 1.73 2.81 4.67 1.33 6.56l-2 2.57-8-6.27 2-2.57c1.47-1.89 4.46-2.02 6.67-.29zM55.3 91.15a6.72 6.72 0 01-1.74.35H46.5" /><path className="cls-5" d="M40.82 46.24l7.96 10 .23-10h4.55V22.6H14.98v23.64h25.84z" /><path className="cls-6" d="M28.67 43.13h14.69l8.33 10.53V43.13h6.22V33.8M26.18 19.49H18.1v24.26M56.67 25.09v-5.6H33.65" /><path className="cls-4" d="M34.17 33.8a5.9 5.9 0 01.34-2.23 4.5 4.5 0 011.24-1.64A6.41 6.41 0 0037 28.62a2.06 2.06 0 00.3-1.07c0-1.12-.52-1.69-1.56-1.69a1.56 1.56 0 00-1.18.46 1.74 1.74 0 00-.46 1.25h-2.9a3.84 3.84 0 011.23-3 4.82 4.82 0 013.31-1.08 4.84 4.84 0 013.29 1 3.64 3.64 0 011.17 2.89 3.57 3.57 0 01-.43 1.62 6.57 6.57 0 01-1.33 1.68l-.81.77a2.78 2.78 0 00-.87 1.71v.61zM34 36.85a1.34 1.34 0 01.43-1 1.64 1.64 0 012.17 0 1.34 1.34 0 01.43 1 1.32 1.32 0 01-.42 1 1.7 1.7 0 01-2.19 0 1.32 1.32 0 01-.42-1z" /><path className="cls-3" d="M79.06 124H66.62v-4.21a6.45 6.45 0 015-6.42 6.29 6.29 0 017.47 6.33zM75.33 122.13v1.25M70.35 122.13v1.25M61.64 124H49.2v-4.21a6.47 6.47 0 015-6.42 6.29 6.29 0 017.47 6.33zM57.91 122.13v1.25M52.93 122.13v1.25" /></svg>
            </div>{/* end hero-content */}
          </div>{/* end container */}
        </section>
        {/*======================================
        END HERO AREA
======================================*/}
        {/* ================================
         START QUESTION AREA
================================= */}
        <section className="question-area pt-80px pb-40px">
          <div className="container">
            <div className="row">
              <div className="col-lg-8">
                <div className="card card-item">
                  <form method="post" className="card-body">
                    <div className="input-box">
                      <label className="fs-14 text-black fw-medium mb-0">Question Title</label>
                      <p className="fs-13 pb-3 lh-20">Be specific and imagine youre asking a question to another person</p>
                      <div className="form-group">
                        <input onChange={inputHandler} value={question.title} name="title" className="form-control form--control" type="text" placeholder="e.g. Is there an R function for finding the index of an element in a vector?" />
                      </div>
                    </div>{/* end input-box */}
                    {/* end input-box */}
                    <div className="input-box">
                      <label className="fs-14 text-black fw-medium mb-0">Category</label>
                      <p className="fs-13 pb-3 lh-20">Please choose the appropriate section so the question can be searched easily.</p>
                      <div className="form-group">
                        <select onChange={inputHandler} name="category" className="select-container select--container" data-placeholder="Select a Category">
                        <option selected value="">Select Category</option>
                            {
                        category.map((value) => {
                                    return (
                                      <option value={value.category_name}>{value.category_name}</option>
        
                                    )
                                })
                              }
                        </select>
                      </div>
                    </div>{/* end input-box */}
                    <div className="input-box">
                      <label className="fs-14 text-black fw-medium mb-0">Details</label>
                      <p className="fs-13 pb-3 lh-20">Include all the information someone would need to answer your question</p>
                      <div className="form-group">
                        <textarea onChange={inputHandler} value={question.description} name="description" className="form-control form--control user-text-editor" rows={10} cols={40} defaultValue={""} />
                        {/* <div className="d-flex align-items-center pt-2">
                          <div className="mr-3">
                            ``` <code className="badge bg-gray border border-gray text-gray">code</code> ```
                          </div>
                          <div className="mr-3 fw-bold">**bold**</div>
                          <div className="mr-3 font-italic">*italic*</div>
                          <div>&gt;quote</div>
                        </div> */}
                      </div>
                    </div>{/* end input-box */}
                    <div className="input-box pt-2">
                      <div className="btn-box">
                        <button type="button" onClick={addQuestionButton} className="btn theme-btn">Publish your question</button>
                      </div>
                    </div>
                  </form>
                </div>{/* end card */}
              </div>{/* end col-lg-8 */}
              <div className="col-lg-4">
                <div className="sidebar">
                  <div className="card card-item p-4">
                    <h3 className="fs-17 pb-3">Step 1: Draft your question</h3>
                    <div className="divider"><span /></div>
                    <p className="fs-14 lh-22 pb-2 pt-3">The community is here to help you with specific coding, algorithm, or language problems.</p>
                    <p className="fs-14 lh-22">Avoid asking opinion-based questions.</p>
                    <div id="accordion" className="generic-accordion pt-4">
                      <div className="card">
                        <div className="card-header" id="headingOne">
                          <button className="btn btn-link fs-15" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                            <span><span className="text-color pr-2 fs-16">1.</span> Summarize the problem</span>
                            <i className="la la-angle-down collapse-icon" />
                          </button>
                        </div>
                        <div id="collapseOne" className="collapse show" aria-labelledby="headingOne" data-parent="#accordion">
                          <div className="card-body">
                            <ul className="generic-list-item generic-list-item-bullet generic-list-item--bullet-2 fs-14">
                              <li className="lh-18 text-black-50">Include details about your goal</li>
                              <li className="lh-18 text-black-50">Describe expected and actual results</li>
                              <li className="lh-18 text-black-50 mb-0">Include any error messages</li>
                            </ul>
                          </div>
                        </div>
                      </div>{/* end card */}
                      <div className="card">
                        <div className="card-header" id="headingTwo">
                          <button className="btn btn-link collapsed fs-15" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                            <span><span className="text-color pr-2 fs-16">2.</span> Describe what youve tried</span>
                            <i className="la la-angle-down collapse-icon" />
                          </button>
                        </div>
                        <div id="collapseTwo" className="collapse" aria-labelledby="headingTwo" data-parent="#accordion">
                          <div className="card-body">
                            <p className="fs-14 lh-22 text-black-50">
                              Show what youve tried and tell us what you found (on this site or elsewhere) and why it didnt meet your needs. You can get better answers when you provide research.
                            </p>
                          </div>
                        </div>
                      </div>{/* end card */}
                      <div className="card">
                        <div className="card-header" id="headingThree">
                          <button className="btn btn-link collapsed fs-15" data-toggle="collapse" data-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                            <span><span className="text-color pr-2 fs-16">3.</span> Show some code</span>
                            <i className="la la-angle-down collapse-icon" />
                          </button>
                        </div>
                        <div id="collapseThree" className="collapse" aria-labelledby="headingThree" data-parent="#accordion">
                          <div className="card-body">
                            <p className="fs-14 lh-22 text-black-50">
                              When appropriate, share the minimum amount of code others need to reproduce your problem (also called a
                              <a href="#" className="text-color hover-underline">minimum</a>, <a href="#" className="text-color hover-underline">reproducible example</a>)
                            </p>
                          </div>
                        </div>
                      </div>{/* end card */}
                    </div>{/* end accordion */}
                  </div>{/* end card */}
                  
                </div>{/* end sidebar */}
              </div>{/* end col-lg-4 */}
            </div>{/* end row */}
          </div>{/* end container */}
        </section>{/* end question-area */}
        {/* ================================
         END QUESTION AREA
================================= */}
        {/* ================================
         START CTA AREA
================================= */}
        <section className="get-started-area pt-80px pb-50px pattern-bg bg-gray">
          <div className="container">
            <div className="text-center">
              <h2 className="section-title">AskTheGeeks Q&amp;A communities are different. <br /> Here's how</h2>
            </div>
            <div className="row pt-50px">
              <div className="col-lg-4 responsive-column-half">
                <div className="card card-item hover-y text-center">
                  <div className="card-body">
                    <img src={bubble} alt="bubble" />
                    <h5 className="card-title pt-4 pb-2">Expert communities.</h5>
                    {/* <p className="card-text">This is just a simple text made for this unique and awesome template, you can easily edit it as you want.</p> */}
                  </div>{/* end card-body */}
                </div>{/* end card */}
              </div>{/* end col-lg-4 */}
              <div className="col-lg-4 responsive-column-half">
                <div className="card card-item hover-y text-center">
                  <div className="card-body">
                    <img src={vote} alt="vote" />
                    <h5 className="card-title pt-4 pb-2">The right answer. Right on top.</h5>
                    {/* <p className="card-text">This is just a simple text made for this unique and awesome template, you can easily edit it as you want.</p> */}
                  </div>{/* end card-body */}
                </div>{/* end card */}
              </div>{/* end col-lg-4 */}
              <div className="col-lg-4 responsive-column-half">
                <div className="card card-item hover-y text-center">
                  <div className="card-body">
                    <img src={check} alt="check" />
                    <h5 className="card-title pt-4 pb-2">Share knowledge. Earn trust.</h5>
                    {/* <p className="card-text">This is just a simple text made for this unique and awesome template, you can easily edit it as you want.</p> */}
                  </div>{/* end card-body */}
                </div>{/* end card */}
              </div>{/* end col-lg-4 */}
            </div>{/* end row */}
          </div>{/* end container */}
        </section>
        {/* ================================
         END CTA AREA
================================= */}
      </div>
        </>
    );
}

export default AddQuestion;