import { useState } from "react";
import axios from "axios";
import React from "react";

function Advertisement() {
  const [preview, setPreview] = useState(null);
  const formData = new FormData();
  const [form, setform] = useState({
    file: null,
    adurl: "",
  });

  const inputHandler = (e) => {
    setform({ ...form, [e.target.name]: e.target.value });
  };

  const fileInputHandler = (e) => {
    setform({ ...form, [e.target.name]: e.target.files[0] });
    setPreview(window.URL.createObjectURL(e.target.files[0]));
  };

  const resetButton = (e) => {
    setform({
      adurl: "",
    });
    setPreview("");
  };

  const addAdvertisement = async (e) => {
    e.preventDefault();
    if (
      form.adurl === "" ||
      form.file === null
    ) {
      alert("Please fill all the fields");
    } else {
      formData.append("file", form.file);
      formData.append("adurl", form.adurl);

      await axios
        .post("https://askthegeeks-nodeserver.onrender.com/add-ad", formData)
        .then((res) => {
          if (res.data === "Ad Added...") {
            alert("Ad Added");
            resetButton();
          }
        });
    }
  };

  return (
    <>
      <section>
        <div className="container">
          <div
            className="tab-pane fade show active"
            id="advertisement-tab"
            role="tabpanel"
            aria-labelledby="edit-profile-tab"
          >
            {/* details start */}
            <div className="user-panel-main-bar">
              <div className="user-panel">
                <div className="bg-gray p-3 rounded-rounded">
                  <h3 className="fs-17 text-center">Add Advertisement</h3>
                </div>
                <form method="post" className="pt-20px">
                  <div className="settings-item">
                    <div className="divider">
                      <span />
                    </div>
                    <div className="row pt-4">
                      <div className="col-lg-6">
                        <div className="input-box">
                          <label className="fs-13 text-black lh-20 fw-medium">
                            Ad Image
                          </label>
                          <div className="form-group">
                            {preview ? (
                              <img
                                src={preview}
                                alt="img"
                                width={200}
                                height={200}
                              />
                            ) : (
                              "Please upload an image"
                            )}
                            <input
                              type="file"
                              name="file"
                              onChange={fileInputHandler}
                            />
                          </div>
                        </div>
                      </div>
                      {/* end col-lg-6 */}

                      <div className="col-lg-6">
                        <div className="input-box">
                          <label className="fs-13 text-black lh-20 fw-medium">
                            Ad url
                          </label>
                          <div className="form-group">
                            <input
                              className="form-control form--control pl-40px"
                              type="text"
                              name="adurl"
                              value={form.adurl}
                              onChange={inputHandler}
                              placeholder="Enter Ad Url"
                            />
                            <span className="la la-pen input-icon" />
                          </div>
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <div className="submit-btn-box pt-3">
                          <button
                            type={"submit"}
                            className="btn theme-btn"
                            onClick={addAdvertisement}
                          >
                            Add Advertisement
                          </button>
                        </div>
                      </div>
                      {/* end col-lg-12 */}
                    </div>
                    {/* end row */}
                  </div>
                </form>
              </div>
              {/* end user-panel */}
            </div>
            {/* end here */}
          </div>
          {/* </li> */}
        </div>
      </section>
    </>
  );
}

export default Advertisement;
