
import {useEffect, useState} from "react";
import axios from "axios";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";

function ViewCategory() {
    let navigate = useNavigate();

    let [users, setUsers] = useState([]);

    function FetchUsers() {
        axios.get("https://askthegeeks-nodeserver.onrender.com/fetch-category")
            .then(res => {
                setUsers(res.data);
            })
    }

    useEffect(() => {
        FetchUsers();
    }, []);


    // DELETE
    function deleteUser(category_name) {
        console.log(category_name);

        Swal.fire({
            title: 'Are you sure to Delete?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
            if (result.isConfirmed) {
                axios.get(`https://askthegeeks-nodeserver.onrender.com/delete-category?category=${category_name}`)
                    .then(res => {
                        // console.log(res.data);

                        if (res.data === "Category Deleted") {
                            FetchUsers();
                            Swal.fire(
                                'Deleted!',
                                'Category has been deleted.',
                                'success'
                            )
                        }
                    });
            }
        })


    }

    function editUser(category_name) {
        let key = 'category_edit';
        localStorage.setItem(key, category_name);
        navigate('/admin/edit-category');
    }

    return(
        <>
            <div className="user-panel-main-bar">
                <div className="user-panel">
                    <div className="bg-gray p-3 rounded-rounded">
                        <h3 className="fs-17 text-center">All Categories</h3>
                    </div>
                    <div className="row pt-4"/>
                    <div className="col-lg-6"/>
                    <form action="#" className="cart-form table-responsive px-2">

                        <table className="table generic-table">
                            <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Category Name</th>
                                <th scope="col">Category Description</th>
                                <th scope="col" className="text-center">Edit</th>
                                <th scope="col" className="text-center">Delete</th>
                            </tr>
                            </thead>
                            <tbody>
                            {
                                users.map((value, index) => {
                                    return (
                                        <tr key={index}>
                                            <td>{index + 1}</td>
                                            <td>{value.category_name}</td>
                                            <td>{value.description}</td>
                                            <td className="text-center">
                                                <button type={"button"} className={"btn btn-primary"} onClick={()=>editUser(value.category_name)}><i className="la la-pen" /></button>
                                            </td>

                                            <td className="text-center">
                                                <button type={"button"} className={"btn btn-danger "} onClick={() => deleteUser(value.category_name)}><i className="la la-trash" /></button>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                            </tbody>
                        </table>
                    </form>
                </div>
                {/* end user-panel */}
            </div>
        </>
    );
}

export default ViewCategory;