import { useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
function AdminFront() {
    const navigate = useNavigate();
    const currentAdmin = localStorage.getItem("admin");
    function adminTypeCheck(currentAdmin){
        axios.get(`https://askthegeeks-nodeserver.onrender.com/get-admin-type?email=${currentAdmin}`)
              .then(res => {
                  console.log(res.data);
                  if (res.data==='admin') {
                      navigate('/mainadmin');
                  } else {
                      navigate('/coadmin');
                  }
              })
    }

    useEffect(() => {
        adminTypeCheck(currentAdmin);
      }, []);

    return
}

export default AdminFront;