import { useState } from "react";
import axios from "axios";
import React from "react";
import Swal from "sweetalert2";

function Signup() {
  const [preview, setPreview] = useState(null);
  const formData = new FormData();
  const [form, setform] = useState({
    username: "",
    member_name: "",
    email: "",
    mobile: "",
    file: null,
    image: "",
    password: "",
    description: "",
  });

  const inputHandler = (e) => {
    setform({ ...form, [e.target.name]: e.target.value });
  };

  const fileInputHandler = (e) => {
    setform({ ...form, [e.target.name]: e.target.files[0] });
    setPreview(window.URL.createObjectURL(e.target.files[0]));
  };

  const resetButton = (e) => {
    setform({
      username: "",
      member_name: "",
      email: "",
      mobile: "",
      password: "",
      description: "",
    });
    setPreview("");
  };

  const addMemberButton = async (e) => {
    e.preventDefault();
    if (
      form.username === "" ||
      form.member_name === "" ||
      form.email === "" ||
      form.mobile === "" ||
      form.password === "" ||
      form.description === "" ||
      form.file === null
    ) {
      alert("Please fill all the fields");
    } else {
      formData.append("file", form.file);
      formData.append("username", form.username);
      formData.append("member_name", form.member_name);
      formData.append("email", form.email);
      formData.append("mobile", form.mobile);
      formData.append("password", form.password);
      formData.append("description", form.description);

      const response = await axios
        .post("https://askthegeeks-nodeserver.onrender.com/member-register-action", formData)
        .then((res) => {
          if (res.data === "duplicate") {
            // alert("Username already exist!!");
            Swal.fire(
              'Error!',
              'Username already exist.',
              'error'
          )
          } else {
            alert("User Registered");
            resetButton();
          }
        });
    }
  };





  return (
    <>
      <div
        className="modal fade modal-container signup-form"
        id="signUpModal"
        tabIndex={-1}
        role="dialog"
        aria-labelledby="signUpModalTitle"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <div className="modal-header align-items-center">
              <h5 className="modal-title" id="signUpModalTitle">
                Welcome! create your account
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true" className="la la-times" />
              </button>
            </div>
            <div className="modal-body">
              <form method="post">

                {preview ? (
        <img src={preview} alt="img" width={200} height={200} />
      ) : (
        "Please upload an image"
      )}
      <input type="file" name="file" onChange={fileInputHandler} />
      
                <div className="form-group">
                  <label className="fs-14 text-black fw-medium lh-18">
                    Username
                  </label>
                  <input
                    className="form-control form--control"
                    type="text"
                    name="username"
                    value={form.username}
                    onChange={inputHandler}
                    placeholder="Your username"
                  />
                </div>
                <div className="form-group">
                  <label className="fs-14 text-black fw-medium lh-18">
                    Name
                  </label>
                  <input
                    className="form-control form--control"
                    type="text"
                    name="member_name"
                    value={form.member_name}
                    onChange={inputHandler}
                    placeholder="Your name"
                  />
                </div>
                <div className="form-group">
                  <label className="fs-14 text-black fw-medium lh-18">
                    Email
                  </label>
                  <input
                    className="form-control form--control"
                    type="email"
                    name="email"
                    value={form.email}
                    onChange={inputHandler}
                    placeholder="Email address"
                  />
                </div>
                <div className="form-group">
                  <label className="fs-14 text-black fw-medium lh-18">
                    Mobile
                  </label>
                  <input
                    className="form-control form--control"
                    type="tel"
                    name="mobile"
                    value={form.mobile}
                    onChange={inputHandler}
                    placeholder="Your number"
                  />
                </div>
                <div className="form-group">
                  <label className="fs-14 text-black fw-medium lh-18">
                    Password
                  </label>
                  <div className="input-group mb-1">
                    <input
                      className="form-control form--control password-field"
                      type="password"
                      name="password"
                      value={form.password}
                      onChange={inputHandler}
                      placeholder="Enter password"
                    />
                    <div className="input-group-append">
                      <button
                        className="btn theme-btn-outline theme-btn-outline-gray toggle-password"
                        type="button"
                      >
                        <svg
                          className="eye-on"
                          xmlns="http://www.w3.org/2000/svg"
                          height="22px"
                          viewBox="0 0 24 24"
                          width="22px"
                          fill="#7f8897"
                        >
                          <path d="M0 0h24v24H0V0z" fill="none" />
                          <path d="M12 6c3.79 0 7.17 2.13 8.82 5.5C19.17 14.87 15.79 17 12 17s-7.17-2.13-8.82-5.5C4.83 8.13 8.21 6 12 6m0-2C7 4 2.73 7.11 1 11.5 2.73 15.89 7 19 12 19s9.27-3.11 11-7.5C21.27 7.11 17 4 12 4zm0 5c1.38 0 2.5 1.12 2.5 2.5S13.38 14 12 14s-2.5-1.12-2.5-2.5S10.62 9 12 9m0-2c-2.48 0-4.5 2.02-4.5 4.5S9.52 16 12 16s4.5-2.02 4.5-4.5S14.48 7 12 7z" />
                        </svg>
                        <svg
                          className="eye-off"
                          xmlns="http://www.w3.org/2000/svg"
                          height="22px"
                          viewBox="0 0 24 24"
                          width="22px"
                          fill="#7f8897"
                        >
                          <path
                            d="M0 0h24v24H0V0zm0 0h24v24H0V0zm0 0h24v24H0V0zm0 0h24v24H0V0z"
                            fill="none"
                          />
                          <path d="M12 6c3.79 0 7.17 2.13 8.82 5.5-.59 1.22-1.42 2.27-2.41 3.12l1.41 1.41c1.39-1.23 2.49-2.77 3.18-4.53C21.27 7.11 17 4 12 4c-1.27 0-2.49.2-3.64.57l1.65 1.65C10.66 6.09 11.32 6 12 6zm-1.07 1.14L13 9.21c.57.25 1.03.71 1.28 1.28l2.07 2.07c.08-.34.14-.7.14-1.07C16.5 9.01 14.48 7 12 7c-.37 0-.72.05-1.07.14zM2.01 3.87l2.68 2.68C3.06 7.83 1.77 9.53 1 11.5 2.73 15.89 7 19 12 19c1.52 0 2.98-.29 4.32-.82l3.42 3.42 1.41-1.41L3.42 2.45 2.01 3.87zm7.5 7.5l2.61 2.61c-.04.01-.08.02-.12.02-1.38 0-2.5-1.12-2.5-2.5 0-.05.01-.08.01-.13zm-3.4-3.4l1.75 1.75c-.23.55-.36 1.15-.36 1.78 0 2.48 2.02 4.5 4.5 4.5.63 0 1.23-.13 1.77-.36l.98.98c-.88.24-1.8.38-2.75.38-3.79 0-7.17-2.13-8.82-5.5.7-1.43 1.72-2.61 2.93-3.53z" />
                        </svg>
                      </button>
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="fs-14 text-black fw-medium lh-18">
                    Bio
                  </label>
                  <input
                    className="form-control form--control"
                    type="text"
                    name="description"
                    value={form.description}
                    onChange={inputHandler}
                    placeholder="Tell about yourself!"
                  />
                </div>
                
                <div className="btn-box">
                  <button
                    type="submit"
                    onClick={addMemberButton}
                    className="btn theme-btn w-100"
                  >
                    Register Account{" "}
                    <i className="la la-arrow-right icon ml-1" />
                  </button>
                </div>

                <p className="create-account-text text-right fs-14">
                  Already on askthegeeks?{" "}
                  <a
                    className="login-btn text-color hover-underline"
                    href="javascript:void(0)"
                  >
                    Log in
                  </a>
                </p>
                {/*<div className="icon-element my-4 mx-auto shadow-sm fs-25">Or</div>*/}
                {/*<div className="text-center">*/}
                {/*    <p className="fs-15 pb-3">Create account with your social network</p>*/}
                {/*    <button className="btn theme-btn bg-8 mb-2 mr-2"><i className="la la-facebook mr-1" /> Facebook</button>*/}
                {/*    <button className="btn theme-btn bg-9 mb-2 mr-2"><i className="la la-twitter mr-1" /> Twitter</button>*/}
                {/*    <button className="btn theme-btn bg-10 mb-2 mr-2"><i className="la la-google mr-1" /> Google</button>*/}
                {/*</div>*/}
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default Signup;
