import { useState , useEffect } from "react";
import axios from "axios";
import { useLocation } from "react-router-dom";
import UserNav from "../users/userNav";
import { useNavigate } from "react-router-dom";
import Sidebar from "../users/Sidebar";
import Swal from "sweetalert2";
function QuestionDetails() {
  const currentUser = localStorage.getItem("username");
  const navigate = useNavigate();
  const location = useLocation();
  let today = new Date();

  let qid= location.state.id;
  console.log("Sidebar qid",qid);
    let [qdetails, setqdetails] = useState([]);
    let [adetails, setAdetails] = useState([]);
    function Fetchqdetails(qid) {
      axios.get(`https://askthegeeks-nodeserver.onrender.com/fetch-question-details?qid=${qid}`)
            .then(res => {
                console.log(res.data);
                setqdetails(res.data);
            })
    }

    function FetchAdetails(qid) {
      axios.get(`https://askthegeeks-nodeserver.onrender.com/fetch-answer-details?qid=${qid}`)
            .then(res => {
                setAdetails(res.data);
            })
    }

    function upVote(id) {
      axios.post(`https://askthegeeks-nodeserver.onrender.com/upvote?id=${id}`)
            .then(res => {
              if (res.data==="upvoted") {
                FetchAdetails(qid);
              }
              else{
              alert("Error Upvote")
              }
            })
    }

    function downVote(id) {
      axios.post(`https://askthegeeks-nodeserver.onrender.com/downvote?id=${id}`)
            .then(res => {
              if (res.data==="downvoted") {
               FetchAdetails(qid);
              }
              else{
              alert("Error Upvote")
              }
            })
    }


    function addquestion(currentUser) {
      axios.get(`https://askthegeeks-nodeserver.onrender.com/get-member-status?username=${currentUser}`)
              .then(res => {
                  console.log(res.data);
                  if (res.data==='active') {
                      navigate('/add-question');
                  } else {
                    Swal.fire(
                      'Banned!',
                      'You are banned to post Questions and Answers.',
                      'error'
                  )
                  }
              })
  }
  function memberProfile(member_username) {
    let key = 'member_username';
                  localStorage.setItem(key, member_username);
                  navigate('/user');
  }

    useEffect(() => {
        Fetchqdetails(qid);
        FetchAdetails(qid);
    }, []);

    const [answer, setAnswer] = useState({
      solution: '',
      question_id: qid,
      member_id: localStorage.getItem('username')
    });
  
    const resetButton = (e) => {
      setAnswer({
          solution: ''
      })
  }
  const inputHandler = (e) => {
      setAnswer({...answer, [e.target.name]: e.target.value})
  }

  const addAnswerButton = async (e) => {
    e.preventDefault()
    if (answer.solution === '') {
        // alert('Please fill all the fields');
        Swal.fire(
          'Answer Empty!',
          'Sorry but empty answers are not solutions.',
          'error'
      )
        
    } else {
        const request = {
            ...answer
        }
        const currentUser = localStorage.getItem("username");
        axios.get(`https://askthegeeks-nodeserver.onrender.com/get-member-status?username=${currentUser}`)
              .then(res => {
                  console.log(res.data);
                  if (res.data==='active') {
                    const response = axios.post('https://askthegeeks-nodeserver.onrender.com/add-answer', request)
                    .then(res =>{
                        if (res.data === "Answer added...") {
                          resetButton()
                          Swal.fire(
                              'Added!',
                              'Answer has been added.',
                              'success'
                          )
                          FetchAdetails(qid);
                        }
                        else {
                            alert("Problem in adding answer")
                        }
                    })
                  } else {
                    Swal.fire(
                      'Banned!',
                      'You are banned to post Questions and Answers.',
                      'error'
                  )
                  }
              })
    }
}
    return(
        <>
        <UserNav/>
            <section className="hero-area bg-white shadow-sm overflow-hidden pt-40px pb-40px">
        <span className="stroke-shape stroke-shape-1" />
        <span className="stroke-shape stroke-shape-2" />
        <span className="stroke-shape stroke-shape-3" />
        <span className="stroke-shape stroke-shape-4" />
        <span className="stroke-shape stroke-shape-5" />
        <span className="stroke-shape stroke-shape-6" />
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-9">
              <div className="hero-content">
                <h2 className="section-title pb-2 fs-24 lh-34">Find the best answer to your technical question, <br />
                  help others answer theirs
                </h2>
                <p className="lh-26">If you are going to use a passage of Lorem Ipsum, you need to be sure there
                  <br /> isn't anything embarrassing hidden in the middle of text.
                </p>
                <ul className="generic-list-item pt-3">
                  <li><span className="icon-element icon-element-xs shadow-sm d-inline-block mr-2"><svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 0 24 24" width="20px" fill="#6c727c"><path d="M0 0h24v24H0V0z" fill="none" /><path d="M11 18h2v-2h-2v2zm1-16C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8zm0-14c-2.21 0-4 1.79-4 4h2c0-1.1.9-2 2-2s2 .9 2 2c0 2-3 1.75-3 5h2c0-2.25 3-2.5 3-5 0-2.21-1.79-4-4-4z" /></svg></span> Anybody can ask a question</li>
                  <li><span className="icon-element icon-element-xs shadow-sm d-inline-block mr-2"><svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 0 24 24" width="20px" fill="#6c727c"><path d="M0 0h24v24H0V0z" fill="none" /><path d="M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z" /></svg></span> Anybody can answer</li>
                  <li><span className="icon-element icon-element-xs shadow-sm d-inline-block mr-2"><svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 0 320 512" width="20px"><path fill="#6c727c" d="M41 288h238c21.4 0 32.1 25.9 17 41L177 448c-9.4 9.4-24.6 9.4-33.9 0L24 329c-15.1-15.1-4.4-41 17-41zm255-105L177 64c-9.4-9.4-24.6-9.4-33.9 0L24 183c-15.1 15.1-4.4 41 17 41h238c21.4 0 32.1-25.9 17-41z" /></svg></span> The best answers are voted up and rise to the top</li>
                </ul>
              </div>{/* end hero-content */}
            </div>{/* end col-lg-9 */}
            <div className="col-lg-3">
              <div className="hero-btn-box text-right py-3">
                <button onClick={()=>addquestion(currentUser)} className="btn theme-btn">Ask a Question</button>
              </div>
            </div>
          </div>{/* end row */}
        </div>{/* end container */}
      </section>


      {/* MAIN HEAD END */}


      {/* QUESTION AREA START */}

      <section className="question-area pt-40px pb-40px">
        <div className="container">
          <div className="row">
            <div className="col-lg-9">
              <div className="question-main-bar mb-50px">


                {/* QUESTION DETAILS START HERE */}


                <div>
                {
                qdetails.map((value) => {
                  const profilePic = value.photo;
                                    return (
                                      <>
                <div className="question-highlight">
                  <div className="media media-card shadow-none rounded-0 mb-0 bg-transparent p-0">
                    <div className="media-body">
                      <h5 className="fs-20"><a href="">{value.title}</a></h5>
                      <div className="meta d-flex flex-wrap align-items-center fs-13 lh-20 py-1">
                        <div className="pr-3">
                          <span>Asked On </span>
                          <span className="text-black">{today.toISOString(value.date_of_posting).split('T')[0]}</span>
                        </div>
                        <div className="pr-3">
                          <span className="pr-1">Viewed</span>
                          <span className="text-black">89 times</span>
                        </div>
                      </div>
                      <div className="tags">
                        <p className="tag-link">{value.category_name}</p>
                      </div>
                    </div>
                  </div>{/* end media */}
                </div>{/* end question-highlight */}
                <div className="question d-flex">
                  <div className="question-post-body-wrap flex-grow-1">
                    <div className="question-post-body">
                      <pre>{value.description}</pre>
                    </div>{/* end question-post-body */}
                    <div className="question-post-user-action">
                      <div className="media media-card user-media owner align-items-center">
                        <a onClick={()=>memberProfile(value.username)} className="media-img d-block">
                          <img src={profilePic} alt="avatar" />
                        </a>
                        <div className="media-body d-flex flex-wrap align-items-center justify-content-between">
                          <div>
                            <p>Asked by:</p>
                            <h5 className="pb-1"><a onClick={()=>memberProfile(value.username)}>{value.username}</a></h5>
                            </div>
                          <small className="meta d-block text-right">
                            <span className="text-black d-block lh-18">asked on</span>
                            <span className="d-block lh-18 fs-12">{today.toISOString(value.date_of_posting).split('T')[0]}</span>
                          </small>
                        </div>
                      </div>{/* end media */}

                    </div>{/* end question-post-user-action */}

                  </div>{/* end question-post-body-wrap */}
                </div>{/* end question */}
                </>
                      )
                                })
                              }
                </div>
                {/* QUESTION DETAILS END HERE */}

                <div className="subheader d-flex align-items-center justify-content-between">
                  <div className="subheader-title">
                    <h3 className="fs-16">{adetails.length} Answers</h3>
                  </div>{/* end subheader-title */}
                  {/* <div className="subheader-actions d-flex align-items-center lh-1">
                    <label className="fs-13 fw-regular mr-1 mb-0">Order by</label>
                    <div className="w-100px">
                      <select className="select-container">
                        <option value="active">active</option>
                        <option value="oldest">oldest</option>
                        <option value="votes" selected="selected">votes</option>
                      </select>
                    </div>
                  </div> */}
                  {/* end subheader-actions */}
                </div>{/* end subheader */}

                <div>
                  {
                adetails.map((value) => {
                  const profilePic = value.photo;
                                    return (
                                      <>
                <div className="answer-wrap d-flex">
                  <div className="votes votes-styled w-auto">
                    <div id="vote2" className="upvotejs">
                      <a onClick={() => upVote(value.id)} className="upvote upvote-on" />
                      <span className="count">{value.votes}</span>
                      <a onClick={() => downVote(value.id)} className="downvote" data-toggle="tooltip" data-placement="right" title="This question is not useful" />
                      {/* <a className="star check star-on" data-toggle="tooltip" data-placement="right" title="The question owner accepted this answer" /> */}
                    </div>
                  </div>{/* end votes */}
                  <div className="answer-body-wrap flex-grow-1">
                    <div className="answer-body">
                    <pre>{value.solution}</pre>
                    </div>{/* end answer-body */}
                    <div className="question-post-user-action">
                      
                      <div className="media media-card user-media align-items-center">
                        <a href="user-profile.html" className="media-img d-block">
                          <img src={profilePic} alt="avatar" />
                        </a>
                        <div className="media-body d-flex align-items-center justify-content-between">
                          <div>
                            <h5 className="pb-1"><a onClick={()=>memberProfile(value.member_id)}>{value.member_id}</a></h5>
                            <div className="stats fs-12 d-flex align-items-center lh-18">
                              <span className="text-black pr-2">15.5k</span>
                              <span className="pr-2 d-inline-flex align-items-center"><span className="ball gold" />3</span>
                              <span className="pr-2 d-inline-flex align-items-center"><span className="ball silver" />10</span>
                              <span className="pr-2 d-inline-flex align-items-center"><span className="ball" />26</span>
                            </div>
                          </div>
                          <small className="meta d-block text-right">
                            <span className="text-black d-block lh-18">answered</span>
                            <span className="d-block lh-18 fs-12">8 hours ago</span>
                          </small>
                        </div>
                      </div>{/* end media */}
                    </div>{/* end question-post-user-action */}
                    
                  </div>{/* end answer-body-wrap */}
                </div>{/* end answer-wrap */}
                </>
                      )
                                })
                              }
                </div>
                <div className="subheader">
                  <div className="subheader-title">
                    <h3 className="fs-16">Your Answer</h3>
                  </div>{/* end subheader-title */}
                </div>{/* end subheader */}
                <div className="post-form">
                  <form method="post" className="pt-3">
                    <div className="input-box">
                      {/* <label className="fs-14 text-black lh-20 fw-medium">Body</label> */}
                      <div className="form-group">
                        <textarea className="form-control form--control fs-13" onChange={inputHandler} value={answer.solution} name="solution" rows={6} placeholder="Your answer here..." />
                      </div>
                    </div>
                    <button className="btn theme-btn theme-btn-sm" onClick={addAnswerButton} type="button">Post Your Answer</button>
                  </form>
                </div>
              </div>{/* end question-main-bar */}
            </div>{/* end col-lg-9 */}
            <Sidebar />
          </div>{/* end row */}
        </div>{/* end container */}
      </section>


      {/* QUESTION AREA END */}
        </>
    );
}

export default QuestionDetails;