import {useNavigate} from "react-router-dom";
import {useEffect, useState} from "react";
import axios from "axios";
import Swal from "sweetalert2";
import EditAdmin from "./EditAdmin";



function ViewAdmin() {
    let navigate = useNavigate();
    let [users, setUsers] = useState([]);

    function FetchUsers() {
        axios.get("https://askthegeeks-nodeserver.onrender.com/send-data")
            .then(res => {
                setUsers(res.data);
            })
    }

    useEffect(() => {
        FetchUsers();
    }, []);


    // DELETE
    function deleteUser(email) {
        console.log(email);

        Swal.fire({
            title: 'Are you sure to Delete?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
            if (result.isConfirmed) {
                axios.get(`https://askthegeeks-nodeserver.onrender.com/delete-email?email=${email}`)
                    .then(res => {
                        // console.log(res.data);

                        if (res.data === "User Deleted") {
                            FetchUsers();
                            Swal.fire(
                                'Deleted!',
                                'Admin has been deleted.',
                                'success'
                            )
                        }
                    });
            }
        })


    }

    function editUser(admin_email) {
        // <EditAdmin admin={admin_email}/>
        let key = 'admin_edit';
        localStorage.setItem(key, admin_email);
        // FetchUsers();
        navigate('/admin/edit-admin')
    }


    return(
        <>
            <div className="user-panel-main-bar">
                <div className="user-panel">
                    <div className="bg-gray p-3 rounded-rounded">
                        <h3 className="fs-17 text-center">All Admins</h3>
                    </div>
                    <div className="row pt-4"/>
                    <div className="col-lg-6"/>
                    <form action="#" className="cart-form table-responsive px-2">

                        <table className="table generic-table">
                            <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Full Name</th>
                                <th scope="col">Email</th>
                                <th scope="col">Mobile</th>
                                <th scope="col">Admin Type</th>
                                <th scope="col" className="text-center">Edit</th>
                                <th scope="col" className="text-center">Delete</th>
                            </tr>
                            </thead>
                            <tbody>
                            {
                                users.map((value, index) => {
                                    return (
                                        <tr key={index}>
                                            <td>{index + 1}</td>
                                            <td>{value.fullname}</td>
                                            <td>{value.email}</td>
                                            <td>{value.mobile}</td>
                                            <td>{value.admintype}</td>  

                                            <td className="text-center">
                                                <button type={"button"} id={value.email} className={"btn btn-primary"} onClick={()=>editUser(value.email)} ><i className="la la-pen" /></button>
                                            </td>

                                            <td className="text-center">
                                                <button type={"button"} className={"btn btn-danger "} onClick={() => deleteUser(value.email)}><i className="la la-trash" /></button>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                            </tbody>
                            {/* <EditAdmin /> */}
                        </table>
                    </form>
                </div>{/* end user-panel */}
            </div>
        </>
    );
}

export default ViewAdmin;