import {useState, useEffect} from "react";
import axios from "axios";
import AdminNav from "./AdminNav";
import { useNavigate } from "react-router-dom";

function EditCategory(){
    let navigate = useNavigate();
    let category = localStorage.getItem('category_edit');
    let [name, setName] = useState("");
    let [description, setDescription] = useState("");

    function GetDetails() {
        axios.get(`https://askthegeeks-nodeserver.onrender.com/fetch-category-data?name=${category}`)
            .then(res => {

                let Category = res.data;
                console.log(res.data);
                setName(Category[0].category_name);
                setDescription(Category[0].description);
            })
    }


    useEffect(() => {
        GetDetails();
      }, []);

    function handleForm(e) {
        e.preventDefault();

        axios.post("https://askthegeeks-nodeserver.onrender.com/update-category", {
            name,
            description
        })
            .then(res => {

                if (res.data === "required") {
                    alert("All fields are required!!");
                } else {
                    setName("");
                    setDescription("");
                    
                    alert(res.data);
                    navigate('/mainadmin');
                    GetDetails();

                }
            })
    }

    return(
        <>
        <AdminNav />
            <div className="modal-body">
            <form method="post" className="pt-20px">
                        <div className="">
                            <div className="divider"><span /></div>
                            <div className="row pt-4">
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Category Name</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" value={name} placeholder="Enter your email address" onChange={(e) => setName(e.target.value)}
                                                   type="text" id="name" name={"category_name"} />
                                            <span className="la la-envelope input-icon" />
                                        </div>
                                    </div>
                                </div>
                                
                                <div className="col-lg-6">
                                    <div className="input-box">
                                        <label className="fs-13 text-black lh-20 fw-medium">Mobile</label>
                                        <div className="form-group">
                                            <input className="form-control form--control pl-40px" value={description} placeholder="Enter your mobile number" onChange={(e) => setDescription(e.target.value)}
                                                   type="mobile" id="mobile" name={"category_description"} />
                                            <span className="la la-mobile input-icon" />
                                        </div>
                                    </div>
                                </div>
                                
                                <div className="col-lg-12">
                                    <div className="submit-btn-box pt-3">
                                        <button type={"button"} className="btn theme-btn" onClick={handleForm}>Update Admin</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </form>
            </div> 
        </>
    );
}

export default EditCategory;